.MainView {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ViewHeader {
  display: flex;
  flex-direction: column;
  width: 92%;
  margin-top: 50px;
}

.ViewHeader h1 {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  color: #D4D6D9;
}

.ViewHeader p {
  font-size: 1.2rem;
  font-weight: 600;
  margin: 5px 0 0 0;
  color: #7D848B;
}

.ViewContent {
  display: flex;
  flex-direction: row;
  width: 92%;
  margin-top: 35px;
  justify-content: space-between;
    flex-wrap: wrap;
}

.Card {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  margin-bottom: 50px;
  width: 32.5%;
}

.Card:hover {
  opacity: .8;
}

.CardImage {
  display: flex;
}

.CardImage img {
  width: 100%;
}

.CardMeta {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #0F161F;
  padding: 5px 12px;
  height: 80px;
}

.CardMeta h3 {
  margin: 0;
  color: #fff;
}

.CardMeta p {
  margin: 5px 0px 0px 0px;
  color: #9FA1A2;
  font-weight: 600;
  font-size: 1rem;
}

.CardInfo {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #07417A;
  padding: 5px 12px;
  height: 80px;
  justify-content: space-between;
}

.CardInfo .SteamLogo {
  height: 40px;
  width: auto;
}

.PriceTags {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
}

.PriceTag {
  padding: 10px 10px;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 700;
}

.DiscountTag {
  background-color: #0B2948;
}

.SaveTag {
  background-color: #118AB2;
}

.FinalTag {
  background-color: #05B083;
}

@media only screen and (max-width: 1024px) {
  .Card {
    width: 49%;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width: 768px) {
  .Card {
    width: 100%;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width: 436px) {
  .CardMeta {
    padding: 12px 12px;
  }

  .CardInfo .SteamLogo {
    height: 30px;
    margin-right: 10px;
  }
}

@media only screen and (max-width: 325px) {
  .CardInfo .SteamLogo {
    display: none;
  }
}